

import React from 'react'
import {motion} from 'framer-motion'
import { FiPhoneCall, FiMail } from "react-icons/fi";



export default function Contact() {

    const [error, setError] = React.useState()

    const [form, setForm ] = React.useState({
        name: '',
        email:'',
        message:''
    })

    function handleChange(event){

        const {name, value, type, checked} = event.target
        setForm(prevForm => {
            return {
                ...prevForm,
                [name] : type === 'checkbox' ? checked : value
            }
        })
    }

   async function handleSubmit (event){
     event.preventDefault()


 
     const details = {
        name: form.name,
        email: form.email,
        message: form.message
     }

     const response = await fetch('/api/message', {
        method: 'POST',
        body: JSON.stringify(details),
        headers: {
        'Content-Type': 'application/json'
        }
     })

     
        const json = await response.json();

        if (response.ok) {
            console.log(json);
            setForm({
                name:"",
                email:"",
                message:""
            })
        } else {
            console.error('Error:', json.error);
        }

        if(form.name ==='' || form.email === '' || form.message === ''){
            setError('Please Enter All fields')
        } else{
            setError('')
        }
    
}
   
  return (
    <div className='contact--div'>
        <div className='contact--title'>
        <motion.div initial={{translateX: -100}} transition={{duration: 0.5}} whileInView={{translateX: 0}}className='info--title'>Lets Work <br></br>Together </motion.div>
        <motion.div initial={{translateX: -100}} transition={{duration: 0.5}} whileInView={{translateX: 0}}className='info--title number'> <FiPhoneCall /> +61 423262136</motion.div>

        <motion.div initial={{translateX: -100}} transition={{duration: 0.5}} whileInView={{translateX: 0}}className='info--title email'> <FiMail /> akmalashwin23@gmail.com </motion.div>
        </div>

        <div className='form--div'>
            <motion.form  initial={{translateX: 200}} transition={{duration:0.5}} whileInView={{translateX: 0}}onSubmit={handleSubmit}>
                <input 
                placeholder='NAME'
                type='text'
                name='name'
                value={form.name}
                onChange={handleChange}

                />
                <input 
                placeholder='EMAIL'
                type='text'
                name='email'
                value={form.email}
                onChange={handleChange}

                />
                <textarea
                placeholder='MESSAGE'
                name='message'
                value={form.message}
                onChange={handleChange}
                >

                </textarea>
                <button className=''>SUBMIT</button>

            </motion.form>
                <div className='error-div'>{error}</div></div>
    </div>
  )
}

